import "./ExpenseItem.css";
import Card from "./utility/Card";
import Date from "./ExpDate";

function ExpenseItem(props) {
  return (
    <div>
      <Card className="Expense__info">
        <div className="upper__components">
          <Date date={props.date} />
          <button className="button__">x</button>
        </div>
        <Card className="expenseDetails">
          <h5> {props.title}</h5>
          <h5>Amount: ${props.amount}</h5>
        </Card>
      </Card>
    </div>
  );
}

export default ExpenseItem;
