import "./Expenses.css";
import ExpenseItem from "./ExpenseItem";

function Expenses(props) {
  let expenseItemOutput = (
    <h5 className="no_expense__found">No expenses found.</h5>
  );

  if (props.selectedYear === "0") {
    expenseItemOutput = props.notFilteredData.map((item) => {
      return (
        //You should only put ONE(<ExpenseItem>) component inside the map method,
        // to make the 'key' property to take effect.
        <ExpenseItem
          key={item.id}
          title={item.title}
          amount={item.amount}
          date={item.date}
        />
      );
    });
  } else {
    if (props.allFilteredData.length !== 0) {
      expenseItemOutput = props.allFilteredData.map((item) => {
        return (
          //You should only put ONE(<ExpenseItem>) component inside the map method,
          // to make the 'key' property to take effect.
          <ExpenseItem
            key={item.id}
            title={item.title}
            amount={item.amount}
            date={item.date}
          />
        );
      });
    }
  }
  return <div>{expenseItemOutput}</div>;
}

export default Expenses;
