import React, { useState } from "react";
import Expenses from "./components/Expenses";
import NewExpense from "./components/New Expense/NewExpense";
import expenses from "./components/SampleData";
import YearFilter from "./components/Year Filter/YearFilter";

function App() {
  const [dataExpenses, setDataExpenses] = useState(expenses);
  const [expenseSelectYear, setExpenseSelectYear] = useState("0");

  const addExpensePointer = (data) => {
    setDataExpenses((previous) => {
      const newData = [...previous, data];
      return newData;
    });
  };

  const getSelectedYear = (data) => {
    setExpenseSelectYear(data);
  };

  const filteredData = dataExpenses.filter(
    (item) => item.date.getFullYear().toString() === expenseSelectYear
  );

  return (
    <div className="App">
      <NewExpense onAddExpense={addExpensePointer} />
      <YearFilter
        onSelectByYear={getSelectedYear}
        yearPasser={expenseSelectYear}
      />
      <Expenses
        allFilteredData={filteredData}
        selectedYear={expenseSelectYear}
        notFilteredData={dataExpenses}
      />
    </div>
  );
}

export default App;
